module utils.math {
    let pointPool: Point[] = [];
    let DEG_TO_RAD: number = Math.PI / 180;

    /**
     * Point  x y 
     */
    export class Point {
        public static release(point: Point): void {
            if (!point) {
                return;
            }
            pointPool.push(point);
        }

        /**
         * Point
         * @param x x0
         * @param y y0
         */
        public static create(x: number, y: number): Point {
            let point = pointPool.pop();
            if (!point) {
                point = new Point();
            }
            return point.setTo(x, y);
        }

        /**
         *  egret.Point .00
         * @param x x0
         * @param y y0
         */
        public constructor(x: number = 0, y: number = 0) {
            this.x = x;
            this.y = y;
        }

        /**
         * 
         * @default 0
         */
        public x: number;

        /**
         * 
         * @default 0
         */
        public y: number;

        /**
         *  (0,0) 
         */
        public get length(): number {
            return Math.sqrt(this.x * this.x + this.y * this.y);
        }

        /**
         *  Point 
         * @param x x
         * @param y y
         */
        public setTo(x: number, y: number): Point {
            this.x = x;
            this.y = y;
            return this;
        }

        /**
         * 
         */
        public clone(): Point {
            return new Point(this.x, this.y);
        }

        /**
         *  x  y 
         * @param toCompare 
         * @returns  Point  true  false
         */
        public equals(toCompare: Point): boolean {
            return this.x == toCompare.x && this.y == toCompare.y;
        }

        /**
         *  pt1  pt2 
         * @param p1 
         * @param p2 
         * @returns 
         */
        public static distance(p1: Point, p2: Point): number {
            return Math.sqrt((p1.x - p2.x) * (p1.x - p2.x) + (p1.y - p2.y) * (p1.y - p2.y));
        }

        /**
         *  Point  Point 
         * @param sourcePoint  Point 
         */
        public copyFrom(sourcePoint: Point): void {
            this.x = sourcePoint.x;
            this.y = sourcePoint.y;
        }

        /**
         * 
         * @param v 
         * @returns 
         */
        public add(v: Point): Point {
            return new Point(this.x + v.x, this.y + v.y);
        }

        /**
         * 
         *  f  pt1  pt2  f  1.0 pt1 f  0 pt2
         * @param pt1 
         * @param pt2 
         * @param f  pt1  pt2  f=1 pt1 f=0 pt2
         */
        public static interpolate(pt1: Point, pt2: Point, f: number): Point {
            let f1: number = 1 - f;
            return new Point(pt1.x * f + pt2.x * f1, pt1.y * f + pt2.y * f1);
        }

        /**
         *  (0,0) 
         * @param thickness  (0,5)  1 (0,1) 
         */
        public normalize(thickness: number): void {
            if (this.x != 0 || this.y != 0) {
                let relativeThickness: number = thickness / this.length;
                this.x *= relativeThickness;
                this.y *= relativeThickness;
            }
        }

        /**
         *  Point dx  x  x dy  y  y 
         * @param dx  x 
         * @param dy  y 
         */
        public offset(dx: number, dy: number): void {
            this.x += dx;
            this.y += dy;
        }

        /**
         * 
         * @param len 
         * @param angle 
         */
        public static polar(len: number, angle: number): Point {
            return new Point(len * Math.cos(angle / DEG_TO_RAD), len * Math.sin(angle / DEG_TO_RAD));
        }

        /**
         * 
         * @param v 
         * @returns 
         * @version Egret 2.4
         * @platform Web,Native
         * @language zh_CN
         */
        public subtract(v: Point): Point {
            return new Point(this.x - v.x, this.y - v.y);
        }

        /**
         *  x  y  "(x=x, y=y)" 23,17  toString()  "(x=23, y=17)"
         * @returns 
         * @version Egret 2.4
         * @platform Web,Native
         * @language zh_CN
         */
        public toString(): string {
            return "(x=" + this.x + ", y=" + this.y + ")";
        }
    }

    /**
     * @private
     * 
     */
    export let $TempPoint = new Point();
}