module utils.math {
    
    let rectanglePool: Rectangle[] = [];
    
    /**
     * Rectangle  (x, y) <br/>
     * Rectangle  xywidth  height 
     * right  bottom  right  width
     *  bottom  height 
     */
    export class Rectangle {
        
        /**
         * Rectangle
         */
        public static release(rect: Rectangle): void {
            if (!rect) {
                return;
            }
            rectanglePool.push(rect);
        }
        
        /**
         * Rectangle
         */
        public static create(): Rectangle {
            let rect = rectanglePool.pop();
            if (!rect) {
                rect = new Rectangle();
            }
            return rect;
        }
        
        public x: number;
        public y: number;
        public width: number;
        public height: number;
        
        public constructor(x: number = 0, y: number = 0, width: number = 0, height: number = 0) {
            this.x = x;
            this.y = y;
            this.width = width;
            this.height = height;
        }
        
        /**
         * x  width 
         */
        public get right(): number {
            return this.x + this.width;
        }
        
        public set right(value: number) {
            this.width = value - this.x;
        }
        
        /**
         * y  height 
         */
        public get bottom(): number {
            return this.y + this.height;
        }
        
        public set bottom(value: number) {
            this.height = value - this.y;
        }
        
        /**
         *  x  Rectangle  left  y  height  width  x  width 
         * left  x 
         */
        public get left(): number {
            return this.x;
        }
        
        public set left(value: number) {
            this.width += this.x - value;
            this.x = value;
        }
        
        /**
         *  y  Rectangle  top  x  width  height  y  height <br/>
         * top  y 
         */
        public get top(): number {
            return this.y;
        }
        
        public set top(value: number) {
            this.height += this.y - value;
            this.y = value;
        }
        
        /**
         *  x  y  Rectangle 
         */
        public get topLeft(): Point {
            return new Point(this.left, this.top);
        }
        
        public set topLeft(value: Point) {
            this.top = value.y;
            this.left = value.x;
        }
        
        /**
         *  right  bottom  Rectangle 
         */
        public get bottomRight(): Point {
            return new Point(this.right, this.bottom);
        }
        
        public set bottomRight(value: Point) {
            this.bottom = value.y;
            this.right = value.x;
        }
        
        /**
         *  Rectangle  Rectangle 
         * @param sourceRect  Rectangle 
         */
        public copyFrom(sourceRect: Rectangle): Rectangle {
            this.x = sourceRect.x;
            this.y = sourceRect.y;
            this.width = sourceRect.width;
            this.height = sourceRect.height;
            return this;
        }
        
        /**
         *  Rectangle 
         * @param x  x 
         * @param y  y 
         * @param width 
         * @param height 
         */
        public setTo(x: number, y: number, width: number, height: number): Rectangle {
            this.x = x;
            this.y = y;
            this.width = width;
            this.height = height;
            return this;
        }
        
        /**
         *  Rectangle 
         * @param x x
         * @param y y
         * @returns truefalse
         */
        public contains(x: number, y: number): boolean {
            return this.x <= x &&
                this.x + this.width >= x &&
                this.y <= y &&
                this.y + this.height >= y;
        }
        
        /**
         *  toIntersect  Rectangle  Rectangle  Rectangle 
         *  Rectangle  0
         * @param toIntersect  Rectangle  Rectangle 
         * @returns  Rectangle  Rectangle  xywidth 
         * height  0 
         */
        public intersection(toIntersect: Rectangle): Rectangle {
            return this.clone().$intersectInPlace(toIntersect);
        }
        
        /**
         *  Rectangle 
         *  Rectangle  dx  dy 
         * @param dx Rectangle 
         * @param dy Rectangle 
         */
        public inflate(dx: number, dy: number): void {
            this.x -= dx;
            this.width += 2 * dx;
            this.y -= dy;
            this.height += 2 * dy;
        }
        
        /**
         * @private
         */
        $intersectInPlace(clipRect: Rectangle): Rectangle {
            let x0 = this.x;
            let y0 = this.y;
            let x1 = clipRect.x;
            let y1 = clipRect.y;
            let l = Math.max(x0, x1);
            let r = Math.min(x0 + this.width, x1 + clipRect.width);
            if (l <= r) {
                let t = Math.max(y0, y1);
                let b = Math.min(y0 + this.height, y1 + clipRect.height);
                if (t <= b) {
                    this.setTo(l, t, r - l, b - t);
                    return this;
                }
            }
            this.setEmpty();
            return this;
        }
        
        /**
         *  toIntersect  Rectangle  Rectangle
         *  xywidth  height  Rectangle 
         * @param toIntersect  Rectangle  Rectangle 
         * @returns truefalse
         */
        public intersects(toIntersect: Rectangle): boolean {
            return Math.max(this.x, toIntersect.x) <= Math.min(this.right, toIntersect.right)
                && Math.max(this.y, toIntersect.y) <= Math.min(this.bottom, toIntersect.bottom);
        }
        
        /**
         *  Rectangle 
         * @returns  Rectangle  0 true  false
         */
        public isEmpty(): boolean {
            return this.width <= 0 || this.height <= 0;
        }
        
        /**
         *  Rectangle  0
         */
        public setEmpty(): void {
            this.x = 0;
            this.y = 0;
            this.width = 0;
            this.height = 0;
        }
        
        /**
         *  Rectangle  xywidth  height  Rectangle 
         * @returns  Rectangle  xywidth  height  Rectangle 
         */
        public clone(): Rectangle {
            return new Rectangle(this.x, this.y, this.width, this.height);
        }
        
        /**
         *  Rectangle 
         *  Rectangle.contains()  Point 
         * @param point 
         * @returns truefalse
         */
        public containsPoint(point: Point): boolean {
            if (this.x <= point.x
                && this.x + this.width > point.x
                && this.y <= point.y
                && this.y + this.height > point.y) {
                return true;
            }
            return false;
        }
        
        /**
         *  Rectangle  rect  Rectangle 
         *  Rectangle  Rectangle  Rectangle  Rectangle
         * @param rect  Rectangle 
         * @returns  Rectangle  Rectangle  true  false
         */
        public containsRect(rect: Rectangle): boolean {
            let r1 = rect.x + rect.width;
            let b1 = rect.y + rect.height;
            let r2 = this.x + this.width;
            let b2 = this.y + this.height;
            return (rect.x >= this.x) && (rect.x < r2) && (rect.y >= this.y) && (rect.y < b2) && (r1 > this.x) && (r1 <= r2) && (b1 > this.y) && (b1 <= b2);
        }
        
        /**
         *  Rectangle 
         * @param {utils.math.Rectangle} rect
         * @returns {boolean}
         */
        public isIntersectRect(rect: Rectangle): boolean {
            return (rect.x >= this.x && rect.x <= this.right) || (rect.y >= this.y && rect.y <= this.bottom)
                || (rect.right >= this.x && rect.right <= this.right) || (rect.bottom >= this.y && rect.bottom <= this.bottom);
        }
        
        /**
         *  toCompare  Rectangle 
         *  xywidth  height  Rectangle 
         * @param toCompare  Rectangle 
         * @returns  Rectangle  xywidth  height  true  false
         */
        public equals(toCompare: Rectangle): boolean {
            if (this === toCompare) {
                return true;
            }
            return this.x === toCompare.x && this.y === toCompare.y
                && this.width === toCompare.width && this.height === toCompare.height;
        }
        
        /**
         *  Rectangle  Rectangle.inflate()  Point 
         * @param point The x property of this Point object is used to increase the horizontal dimension of the Rectangle object. The y property is used to increase the vertical dimension of the Rectangle object.
         */
        public inflatePoint(point: Point): void {
            this.inflate(point.x, point.y);
        }
        
        /**
         *  Rectangle 
         * @param dx  Rectangle  x 
         * @param dy  Rectangle  t 
         */
        public offset(dx: number, dy: number): void {
            this.x += dx;
            this.y += dy;
        }
        
        /**
         *  Point  Rectangle  Rectangle.offset()  Point 
         * @param point  Rectangle  Point 
         */
        public offsetPoint(point: Point): void {
            this.offset(point.x, point.y);
        }
        
        /**
         *  Rectangle 
         * @returns  Rectangle xywidth  height
         */
        public toString(): string {
            return "(x=" + this.x + ", y=" + this.y + ", width=" + this.width + ", height=" + this.height + ")";
        }
        
        /**
         *  Rectangle 
         * @param toUnion  Rectangle  Rectangle 
         * @returns  Rectangle 
         */
        public union(toUnion: Rectangle): Rectangle {
            let result = this.clone();
            if (toUnion.isEmpty()) {
                return result;
            }
            if (result.isEmpty()) {
                result.copyFrom(toUnion);
                return result;
            }
            let l: number = Math.min(result.x, toUnion.x);
            let t: number = Math.min(result.y, toUnion.y);
            result.setTo(l, t,
                Math.max(result.right, toUnion.right) - l,
                Math.max(result.bottom, toUnion.bottom) - t);
            return result;
        }
        
        /**
         * @private
         */
        $getBaseWidth(angle: number): number {
            let u = Math.abs(Math.cos(angle));
            let v = Math.abs(Math.sin(angle));
            return u * this.width + v * this.height;
        }
        
        /**
         * @private
         */
        $getBaseHeight(angle: number): number {
            let u = Math.abs(Math.cos(angle));
            let v = Math.abs(Math.sin(angle));
            return v * this.width + u * this.height;
        }
    }
    
    /**
     * @private
     * 
     */
    export let $TempRectangle = new Rectangle();
}